<template>
   <div>
      <section class="bg-white text-white pt-8">

         <h2 class="uppercase text-3xl pt-6 text-blue font-bold
            md:text-6xl
            lg:text-4xl">- hotel -</h2>

            <span class="capitalize font-Clarissa text-blue text-5xl">{{country}}</span>

         <figure class="pt-16">
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure>

         
 
         <div class="bg-blue-180 py-10">         
            <!-- BUSCADOR DE PRECIOS -->
            <div class="flex flex-row justify-center items-end gap-8 border border-white rounded-4xl py-6 m-auto
            md:text-xl md:w-9/12">
                <div class="flex flex-col items-start gap-1">
                  <span class="font-semibold text-base text-left px-4">Rango de fecha a reservar</span>
                  <date-range-picker                    
                     ref="picker"
                     :locale-data="{ firstDay: 1, format: 'yyyy-mm-dd' }"
                     v-model="dateRange"
                     :time-picker="false"
                     :autoApply="true"
                     :ranges="false"
                     class="dateRangePicker drp-detail"
                  >
                     <template #input="picker">
                        <span class="text-white text-xl w-full text-center">
                              {{ picker.startDate | dateFormat }} - {{ picker.endDate | dateFormat }}
                        </span>
                     </template>                
                  </date-range-picker>     
               </div>

               <stars v-on:getStar="updStar" :star_style="'block'"></stars>       

               <div class="relative flex flex-col">
                  <span class="font-semibold text-base text-left leading-7 px-4">Habitaciones</span>
                  <div class="flex flex-col items-center justify-around gap-x-2 bg-transparent border border-white rounded-4xl text-white px-4 py-1
               md:flex-row">  
                        <i class="fas fa-door-open"></i>
                        <span> {{rooms}} Habitacion </span>
                        <i class="fa fa-user"></i>
                        <span> {{numperson}} Personas </span>
                        <div class="cursor-pointer text-orange text-xl
                        md:ml-auto" @click="visible_person = !visible_person">
                           <i class="fas fa-edit"></i>
                        </div>                        
                  </div>
                  <div id="rooms" class="absolute right-0 flex gap-x-3 bg-white border-lightGray text-darkGray rounded-3xl px-3"
                     :class="{'visible-numperson': visible_person}" >                            
                           <!-- rooms -->
                           <number-imput :title="'Habitaciones'" :min="1" @updVal="(val) => {this.rooms = val}"></number-imput>
                           <!-- adults -->
                           <number-imput :title="'Adultos'" :min="1" @updVal="(val) => {this.adults = val}"></number-imput>
                           <!-- children -->
                           <number-imput :title="'Niños'" @updVal="(val) => {this.children = val}"></number-imput>
                           <!-- infants -->
                           <number-imput :title="'Infantes'" @updVal="(val) => {this.infants = val}"></number-imput>
                           <span class="cursor-pointer h-auto" @click="visible_person = !visible_person"><i class="fa fa-times"></i></span>
                  </div>                                   
               </div>

               <button class="uppercase bg-orange text-white text-xl border border-white rounded-4xl px-4 py-1">buscar</button>               
            </div>
            
            <!-- DETALLE DEL HOTEL -->
            <div class="container flex flex-col gap-4 p-10 md:flex-row">
               <!-- GALERIA DE IMAGENES -->
               <Splide class="hotel-slide-gallery" :options="{    
                  direction   : 'ttb',                                                   
                  rewind      : true,
                  wheel       : true,
                  pagination  : false,
                  autoplay    : true,
                  arrows      : false,
                  perPage     : 3,                  
                  height       : '50rem',
                  width       : '20rem',
                  fixedWidth  : '15rem',
                  fixedHeight  : '15rem'
               
               }">
                  <SplideSlide v-for="(item, index) in hotelInfo.images" :key="index">
                     <img :src="item.img" :alt="'Sample' + index">
                  </SplideSlide>                     
               </Splide>

               <!-- INFORMACION DEL HOTEL -->
               <article class="flex flex-col gap-3 border-2 border-dashed border-white p-2
                  w-10/12">
                  <div class="flex flex-col gap-3 p-2
                  md:w-auto md:flex-row">
                     <!-- Info 7 Price From -->
                     <div class="flex flex-col gap-y-3 px-2 py-2
                     md:w-7/12 md:items-start">
                        <h2 class="uppercase text-2xl font-DIN-Bold
                        md:text-3xl
                        ">Iberostar laguna azul</h2>
                        <small>Desde <b class="text-orange text-xl font-DIN-Bold
                        md:text-2xl
                        ">$59.99</b> [por persona / la noche]</small>                     
                     </div>
                     <!-- Stars -->
                     <stars class="self-start" :star_init="4"></stars>
                     <!-- Segment -->
                     <figure class="w-4/12 m-auto md:w-1/6 md:ml-auto">
                        <img :src="hotelInfo.segment.img" alt="">
                     </figure>
                  </div>
                  <!-- THINGS TO DO & AMENITIES -->                  
                  <div class="grid grid-cols-2 gap-3 justify-items-start px-4">
                     <div>
                        <h3 class="title">Cosas para hacer</h3>
                        <ul>
                           <li>26 hour fitness center</li>
                        </ul>
                     </div>
                     <div>
                        <h3 class="title">Amenities</h3>
                        <ul>
                           <li v-for="(item, index) in hotelInfo.amenities" :key="index">
                              <i class="icon-cafe"></i>
                           </li>
                        </ul>
                     </div>
                  </div>
               </article>               
            </div>
         </div>
      </section>
   </div>
</template>
<script>
import DateRangePicker from 'vue2-daterange-picker'
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css'

import NumberImput from '@/components/NumberImput'
import Stars from '@/components/searcher/Stars'
import moment from 'moment'

import { Splide, SplideSlide } from '@splidejs/vue-splide';
import '@splidejs/splide/dist/css/themes/splide-default.min.css';

import '@/assets/css/fonts.css'
export default {
   name: 'ProductsDetail',
   filters: {
        dateFormat(value) {
        if (value) {
            return moment(String(value)).format('DD MMM YYYY')
        }
        }
   },
   components: {
      DateRangePicker,
      NumberImput,
      Stars,
      Splide,
      SplideSlide
   },
   data() {
      return {
         slide_options: { 
            rewind      : true,
            direction   : 'ttb',
            wheel       : true,
            pagination  : false,
            autoplay    : true,
            arrows      : false,
            perPage     : 3,
            height      : '14rem',
            width       : '20rem',                            
         },
         country: '',         
         visible_person: false,
         rooms: 1,
         adults: 1,
         children: 0,
         infants: 0,
         no_star: 3,
         hotelInfo: {},
         dateRange: {
                startDate: moment().add(3, 'days').format('DD MMM YYYY'),
                endDate: moment().add(4, 'days').format('DD MMM YYYY')
         },         
      }
   },
   computed: {
        numperson() {
            return this.adults + this.children + this.infants
        }
   },
   created() {
      this.country = this.$route.params.poleKey
      this.getHotelinfo()
   },
   methods: {
      getHotelinfo() {
         const am = 'iron'
         const amenities = [
            require("@/assets/img/amenities/" + am + ".png")
         ]

         this.hotelInfo = {
            keyword: 'iberostar-laguna-azul',
            name: "Iberostar Laguna Azul",          
            price_from: '59.99',
            star: '59.99',
            segment: {
               name: "playa",
               img: require("@/assets/img/segment/playa.png")
            }, 
            amenities, 
            images: [
               {img: require("@/assets/img/block1.png")},
               {img: require("@/assets/img/block2.png")},
               {img: require("@/assets/img/block3.png")},
               {img: require("@/assets/img/block2.png")},
               {img: require("@/assets/img/block1.png")},
            // {img: 'http://iristravel-gallery.com/photos/hotel/iberostar-laguna-azul-1.jpg'},
            // {img: 'http://iristravel-gallery.com/photos/hotel/iberostar-laguna-azul-2.jpg'},
            // {img: 'http://iristravel-gallery.com/photos/hotel/iberostar-laguna-azul-3.jpg'},
            // {img: 'http://iristravel-gallery.com/photos/hotel/iberostar-laguna-azul-4.jpg'},
            // {img: 'http://iristravel-gallery.com/photos/hotel/iberostar-laguna-azul-5.jpg'}
            ]
         }
      },
      updStar(no) {
         this.no_star = no
      }, 
   }
}
</script>
<style scoped>
   .title {
      text-transform: uppercase;
      font-family: 'DINPro-Bold';
      font-size: 18px;
      margin-bottom: 5px;
   }

    #rooms {
        visibility: collapse;
        opacity: 0;
        bottom: -100px;
        padding-top: 0.5rem;    
        padding-bottom: 0.5rem;    
        border: 1px solid var(--light-gray);
    }

    .visible-numperson {                
        visibility: visible !important;
        opacity: 1 !important;
        transition: opacity 150ms;        
        transition-timing-function: cubic-bezier(.22,.48,.82,.61);
    }
    @media screen and (min-width: 768px) {
        .visible-form {
            height: 260px !important;
        }
    }
</style>